<template>
    <p class="keywords"><span v-for="keyword in article.keywords" :key="keyword">{{keyword}}</span></p>
    <h3>{{article.title}}</h3>
    <h4>{{article.subtitle}}</h4>
    <div class="banner">
        <img :src="'https://horizontes.herokuapp.com/images/articles/' + article.banner" :alt="article.banner">
        <figcaption>{{article.figcaptionBanner}}</figcaption>
    </div>
    <div class="description">
        <img :src="'https://horizontes.herokuapp.com/images/profiles/' + article.authorProfile" :alt="article.authorProfile">
        <p>de <span class="author">{{article.author}}</span>, el <span>{{article.date}}</span></p>    
    </div>
    <article>
        <p>
            <strong>{{article.intro}}</strong>
        </p>           
        <p>
            {{article.text}}
        </p>
        <div class="illustration">
            <img src="" :alt="article.illustration">
            <figcaption>{{article.figcaptionIllustration}}</figcaption>
        </div>
        <p>
            {{article.text2}}
        </p>
        <div class="quote">
            <q>
                {{article.quote}}                       
            </q>
            <br>
            <cite>{{article.quoteAuthor}}</cite>    
        </div>        
        <p>
            {{article.text3}}
        </p>
        <p>
            {{article.conclusion}}
        </p>
        <p class="credits">{{article.author}} para <span>HORIZONTES_vlc</span></p>   
    </article>        
</template>

<script>
export default {
    name: "ArticleComp",
    props: {
        article: Object
    },
    created() {
        console.log(this.article)
    }
}
</script>

<style lang="scss" scoped>
