<template>
    <Nav />
    <h2>ARTÍCULOS / CULTURA</h2>
    <main>
        <ArticleComp :article="this.article" />  
    </main>
    <Footer />
</template>

<script>
import apiUrl from "../../config.js"
import Nav from "../components/Nav.vue"
import ArticleComp from "../components/Article-Comp.vue"
import Footer from "../components/Footer.vue"

export default {
    name: "Article",
    components: {
        Nav,
        ArticleComp,
        Footer
    },
    data() {
        return {
            article: {}
        }
    },
    methods: {
        async fetchArticle() {
            const parsedUrl = new URL(window.location.href)
            const articleId = parsedUrl.hash.split('/article/')[1]
            const res = await fetch(`${apiUrl}/api/articles/${articleId}`)
            const data = await res.json()
            return data
        }
    },
    async created() {
        this.article = await this.fetchArticle()
    }
}
</script>